<template>
  <div class="goods">
    <div class="menu-wrapper" v-el:menu-wrapper>
      <ul>
        <li v-for="item in goods" class="menu-item" :class="{current:currentIndex == $index}" @click="selectMenu($index,$event)">
          <span class="text border-1px">
            <span class="icon" v-show="item.type>0" :class="classMap[item.type]"></span>
            {{item.name}}
          </span>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" v-el:foods-wrapper>
      <ul>
        <li class="food-list food-list-hook" v-for="item in goods">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li class="food-item border-1px"
              v-for="food in item.foods"
              @click="selectFood(food,$event)">
              <div class="icon">
                <img :src="food.icon" width="57px"/>
              </div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span class="count">月售{{food.sellCount}}份</span>
                  <span>好评率{{food.rating}}%</span>
                </div>
                <div class="price">
                  <span class="now">￥{{food.price}}</span>
                  <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol :food="food"></cartcontrol>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shopcart
      v-ref:shopcart
      :select-foods="selectFoods"
      :delivery-price="seller.deliveryPrice"
      :min-price="seller.minPrice">
    </shopcart>
    <food :food="selectedFood" v-ref:food></food>
  </div>
</template>

<script>
import sllers from '../../../data.json';
import BScroll from 'better-scroll';
import shopcart from '../shopcart/shopcart';
import cartcontrol from '../cartcontrol/cartcontrol';
import food from '../food/food';
export default{
  data() {
    return {
      goods: {},
      listHeight: [],
      scrollY: 0,
      selectedFood: {}
    };
  },
  props: {
    seller: {
      type: Object
    }
  },
  computed: {
    currentIndex() {
      for (let i = 0; i < this.listHeight.length; i++) {
        let height = this.listHeight[i];
        let height2 = this.listHeight[i + 1];
        if (!height2 || (this.scrollY >= height && this.scrollY < height2)) {
          return i;
        }
      }
      return 0;
    },
    selectFoods() {
      let foods = [];
      this.goods.forEach((good) => {
        good.foods.forEach((food) => {
          if (food.count) {
            foods.push(food);
          }
        });
      });
      return foods;
    }
  },
  created() {
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    // this.$http.get('/api/goods').then(
    //   (req) => {
    //     req = req.body;
    //     if (req.errno === 0) {
    //       this.goods = req.data;
    //       this.$nextTick(() => {
    //         this._initScroll();
    //         this._calcaulateHeight();
    //       });
    //     }
    //   }
    // );
    this.goods = sllers.goods;
    this.$nextTick(() => {
      this._initScroll();
      this._calcaulateHeight();
    });
  },
  methods: {
    _initScroll() {
      this.menuScroll = new BScroll(this.$els.menuWrapper, {
        click: true
      });
      this.foodScroll = new BScroll(this.$els.foodsWrapper, {
        probeType: 3,
        click: true
      });
      this.foodScroll.on('scroll', (pos) => {
        this.scrollY = Math.abs(Math.round(pos.y));
      });
    },
    _calcaulateHeight() {
      let foodList = this.$els.foodsWrapper.getElementsByClassName('food-list-hook');
      let height = 0;
      this.listHeight.push(height);
      for (let i = 0; i < foodList.length; i++) {
        let item = foodList[i];
        height += item.clientHeight;
        this.listHeight.push(height);
      }
    },
    selectMenu(index, event) {
      if (!event._constructed) {
        return !1;
      }
      let foodList = this.$els.foodsWrapper.getElementsByClassName('food-list-hook');
      let el = foodList[index];
      this.foodScroll.scrollToElement(el, 300);
    },
    selectFood(food, event) {
      if (!event._constructed) {
        return !1;
      }
      this.selectedFood = food;
      this.$refs.food.show();
    },
    _drop(target) {
      // 体验优化 异步执行
      this.$nextTick(() => {
        this.$refs.shopcart.drop(target);
      });
    }
  },
  components: {
    shopcart,
    cartcontrol,
    food
  },
  events: {
    'cart.add'(target) {
      this._drop(target);
    }
  }
};
</script>
<style lang="less" rel="stylessheet/less">
  @import "../../common/less/mixin.less";
  .goods{
    display:flex;
    position:absolute;
    top:174px;
    bottom:46px;
    width:100%;
    overflow:hidden;
    .menu-wrapper{
      flex:0 0 80px;
      width:80px;//不写这个width在安卓下会有问题
      background:#f3f5f7;
      .menu-item{
        display:table;
        height:54px;
        width:56px;
        padding:0 12px;
        line-height:14px;
        &.current{
          position: relative;
          z-index:10;
          background:#fff;
          font-weight:700;
          .text{
            border:none;
          }
        }
        .icon{
          display:inline-block;
          vertical-align:middle;
          height:12px;
          width:12px;
          margin-right:2px;
          background-size:12px 12px;
          background-repeat:no-repeat;
          &.decrease{
            .bg-img('../../components/goods/decrease_3');
          }
          &.discount{
            .bg-img('../../components/goods/discount_3');
          }
          &.guarantee{
            .bg-img('../../components/goods/guarantee_3');
          }
          &.invoice{
            .bg-img('../../components/goods/invoice_3');
          }
          &.special{
            .bg-img('../../components/goods/special_3');
          }
        }
        .text{
          display:table-cell;
          width:56px;
          vertical-align:middle;
          font-size:12px;
          .border-1px(rgba(7,17,27,0.1));
        }
      }
    }
    .foods-wrapper{
      flex:1;
      .title{
        padding-left:17px;
        height:26px;
        line-height:26px;
        border-left:2px solid #d9dde1;
        font-size:12px;
        color:rgb(147,153,159);
        background-color:#f3f5f7;
      }
      .food-item{
        display:flex;
        margin:18px;
        padding-bottom:18px;
        .border-1px(rgba(7,17,27,0.1));
        &:last-child{
          .border-none();
          margin-bottom:0;
        }
        .icon{
          flex:0 0 57px;
          margin-right:10px;
        }
        .content{
          flex:1;
          .name{
            margin:2px 0 8px;
            height:14px;
            line-height:14px;
            font-size:14px;
            color:rgb(7,17,27);
          }
          .desc,.extra{
            line-height:10px;
            font-size:10px;
            color:rgb(147,153,159);
          }
          .desc{
            line-height:12px;
            margin-bottom:8px;
          }
          .extra{
            &>.count{
              margin-right:12px;
            }
          }
          .price{
            font-weight:700;
            line-height:24px;
            .now{
              margin-right:8px;
              font-size:14px;
              color:rgb(240,20,20);
            }
            .old{
              text-decoration:line-through;
              font-size:10px;
              color:rgb(147,153,159);
            }
          }
          .cartcontrol-wrapper{
            position:absolute;
            right:0;
            bottom:12px;
          }
        }
      }
    }

  }
</style>



