<template>
  <div class="seller" v-el:seller>
      <div class="seller-content">
        <div class="overview">
          <h1 class="title">{{seller.name}}</h1>
          <div class="desc border-1px">
            <star :size="36" :score="seller.score"></star>
            <span class="text">({{seller.ratingCount}})</span>
            <span class="text">月售{{seller.sellCount}}单</span>
          </div>
          <ul class="remark">
            <li class="block">
              <h2>起送价</h2>
              <div class="content">
                <span class="stress">{{seller.minPrice}}</span>元
              </div>
            </li>
            <li class="block">
              <h2>商家配送</h2>
              <div class="content">
                <span class="stress">{{seller.deliveryPrice}}</span>元
              </div>
            </li>
            <li class="block">
              <h2>平均配送时间</h2>
              <div class="content">
                <span class="stress">{{seller.deliveryTime}}</span>分钟
              </div>
            </li>
          </ul>
          <div class="favorite">
            <span class="icon-favorite"
              :class="{'active':favorite}"
              @click="_toggleFavorite"></span>
            <span class="text">{{favoriteText}}</span>
          </div>
        </div>
        <split></split>
        <div class="bulletin">
          <h1 class="title">公告与活动</h1>
          <div class="content-wrapper border-1px">
            <p class="content">{{seller.bulletin}}</p>
          </div>
          <ul v-if="seller.supports" class="supports">
            <li class="support-item border-1px" v-for="item in seller.supports">
              <span class="icon" :class="classMap[seller.supports[$index].type]"></span>
              <span class="text">{{seller.supports[$index].description}}</span>
            </li>
          </ul>
        </div>
        <split></split>
        <div class="pics">
          <h1 class="title">商家实景</h1>
          <div class="pic-wrapper" v-el:pic-wrapper>
            <ul class="pic-list" v-el:pic-list>
              <li class="pic-item"
                v-for="pic in seller.pics">
                <img :src="pic" width="120" height="90" />
              </li>
            </ul>
          </div>
        </div>
        <split></split>
        <div class="info">
          <h1 class="title border-1px">商家信息</h1>
          <ul>
            <li class="info-item"
              v-for="info in seller.infos">
              {{info}}
            </li>
          </ul>
        </div>
      </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll';
import {saveToLocal, loadFromLocal} from '../../common/js/store';
import star from 'components/star/star';
import split from 'components/split/split';
export default{
  data() {
    return {
      favorite: (() => {
        return loadFromLocal(this.seller.id, 'favorite', false);
      })()
    };
  },
  props: {
    seller: {
      type: Object
    }
  },
  watch: {
    'seller'() {
      this._initScroll();
      this._initPic();
    }
  },
  methods: {
    _initScroll() {
      if (!this.scroll) {
        this.scroll = new BScroll(this.$els.seller, {
          click: true
        });
      } else {
        this.scroll.refresh();
      }
    },
    _initPic() {
      if (this.seller.pics) {
        let picWith = 120;
        let margin = 6;
        let width = (picWith + margin) * this.seller.pics.length - margin;
        this.$els.picList.style.width = width + 'px';
        this.$nextTick(() => {
          if (!this.picScroll) {
            this.picScroll = new BScroll(this.$els.picWrapper, {
              scrollX: true,
              eventPassthrough: 'vertical'
            });
          } else {
            this.picScroll.refresh();
          }
        });
      }
    },
    _toggleFavorite(event) {
      if (event._construted) {
        return;
      }
      this.favorite = !this.favorite;
      saveToLocal(this.seller.id, 'favorite', this.favorite);
    }
  },
  computed: {
    favoriteText() {
      return this.favorite && '已收藏' || '收藏';
    }
  },
  created() {
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
  },
  ready() {
    // created不能确保dom已经加载完成
    // seller数据异步加载 也不能保证在dom加载前获取
    // 组件切换造成整个dom替换  因此再次切换回来时 要在ready执行
    // 然而ready优先于watch ready执行后数据没加载，dom没撑开，还是回到了第二条注释
    // 因此在_initScroll 方法中加一个refresh刷新功能
    this._initScroll();
    this._initPic();
  },
  components: {
    star,
    split
  }
};
</script>
<style lang="less" rel="stylessheet/less">
@import "../../common/less/mixin.less";
.seller{
  position:absolute;
  top:174px;
  bottom:0;
  left:0;
  width:100%;
  overflow:hidden;
  .overview{
    padding:18px;
    .title{
      margin-bottom:8px;
      line-height:14px;
      color:rgb(7,17,27);
      font-size:14px;
    }
    .desc{
      padding-bottom:18px;
      font-size:0;
      .border-1px(rgba(7,17,27,0.1));
      .star{
        display:inline-block;
        margin-right:8px;
        vertical-align:top;
      }
      .text{
        display:inline-block;
        margin-right:12px;
        vertical-align:top;
        font-size:10px;
        line-height:18px;
        color:rgb(77,85,93);
      }
    }
    .remark{
      display:flex;
      padding-top:18px;
      .block{
        flex:1;
        text-align:center;
        border-right:1px solid rgba(7,17,27,0.1);
        &:last-child{
          border-right:none;
        }
        h2{
          margin-bottom:4px;
          line-height:10px;
          font-size:10px;
          color:rgb(147,153,159);
        }
        .content{
          line-height:24px;
          font-size:10px;
          color:rgb(7,17,27);
          .stress{
            font-size:24px;
          }
        }
      }
    }
    .favorite{
      position:absolute;
      right:11px;
      top:18px;
      width:50px;
      text-align:center;
      .icon-favorite{
        display:block;
        margin-bottom:4px;
        font-size:24px;
        line-height:24px;
        color:#d4d6d9;
        &.active{
          color:rgb(240,20,20);
        }
      }
      .text{
        font-size:10px;
        line-height:10px;
        color:rgb(77,85,93);
      }
    }
  }
  .bulletin{
    padding:18px 18px 0 18px;
    .title{
      margin-bottom:8px;
      line-height:14px;
      color:rgb(7,17,27);
      font-size:14px;
    }
    .content-wrapper{
      padding:0 12px 16px 12px;
      .border-1px(rgba(7,17,27,0.1));
      .content{
        line-height:24px;
        font-size:12px;
        color:rgb(240,20,20);
      }
    }
    .supports{
      .support-item{
        padding:16px 12px;
        .border-1px(rgba(7,17,27,0.1));
        font-size:0;
        &:last-child{
          .border-none();
        }
      }
      .icon{
        display:inline-block;
        width:16px;
        height:16px;
        margin-right:6px;
        background-size:16px 16px;
        background-repeat:no-repeat;
        vertical-align:top;
        &.decrease{
          .bg-img('../../components/header/decrease_4');
        }
        &.discount{
          .bg-img('../../components/header/discount_4');
        }
        &.guarantee{
          .bg-img('../../components/header/guarantee_4');
        }
        &.invoice{
          .bg-img('../../components/header/invoice_4');
        }
        &.special{
          .bg-img('../../components/header/special_4');
        }
      }
      .text{
        line-height:16px;
        font-size:12px;
        color:rgb(7,17,27);
      }
    }
  }
  .pics{
    padding:18px;
    .title{
      margin-bottom:12px;
      line-height:14px;
      color:rgb(7,17,27);
      font-size:14px;
    }
    .pic-wrapper{
      width:100%;
      overflow:hidden;
      white-space:nowrap;
      .pic-list{
        font-size:0;
        .pic-item{
          display:inline-block;
          margin-right:6px;
          width:120px;
          height:90px;
          &:last-child{
            margin:0;
          }
        }
      }
    }
  }
  .info{
    padding:18px 18px 0 18px;
    color:rgb(7,17,27);
    .title{
      padding-bottom:8px;
      line-height:14px;
      .border-1px(rgba(7,17,27,0.1));
      font-size:14px;
    }
    .info-item{
      padding:6px 12px;
      line-height:16px;
      .border-1px(rgba(7,17,27,0.1));
      font-size:12px;
      &:last-child{
        .border-none();
      }
    }
  }
}
</style>



