<template>
  <div class="food"
    v-el:food
    v-show="foodFlag"
    transition="move">
    <div class="food-content">
      <div class="image-header">
        <img :src="food.image" />
        <div class="back" @click="hide"><i class="icon-arrow_lift"></i></div>
      </div>
      <div class="content">
        <div class="title">{{food.name}}</div>
        <div class="detail">
          <span class="sell-count">月售{{food.sellCount}}份</span>
          <span class="rating">好评{{food.rating}}%</span>
        </div>
        <div class="price">
          <span class="now">￥{{food.price}}</span>
          <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
        </div>
        <div class="cartcontrol-wrapper">
          <cartcontrol :food="food"></cartcontrol>
        </div>
        <div class="buy"
          transition="fade"
          v-show="!food.count || food.count === 0"
          @click.stop.prevent="addFirst">
          加入购物车
        </div>
      </div>
      <split v-show="food.info"></split>
      <div class="info" v-show="food.info">
        <h1 class="title">商品信息</h1>
        <p class="text">{{food.info}}</p>
      </div>
      <split></split>
      <div class="rating">
        <h1 class="title">商品评价</h1>
        <ratingselect
          :select-type="selectType"
          :only-content="onlyContent"
          :desc="desc"
          :ratings="food.ratings"></ratingselect>
      </div>
      <div class="rating-wrapper">
        <ul v-show="food.ratings && food.ratings.length">
          <li class="rating-item border-1px"
            v-show="needShow(rating.rateType,rating.text)"
            v-for="rating in food.ratings">
            <div class="user">
              <span class="name">{{rating.username}}</span>
              <img class="avatar" width="12" :src="rating.avatar">
            </div>
            <div class="time">{{rating.rateTime | formatDate}}</div>
            <p class="text">
              <span :class="{'icon-thumb_up':rating.rateType === 0,'icon-thumb_down':rating.rateType === 1}"></span>
              {{rating.text}}
            </p>
          </li>
        </ul>
        <div class="no-rating" v-show="!(food.ratings && food.ratings.length)">
          暂无评价
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import BScroll from 'better-scroll';
import {formatDate} from 'common/js/date';
import split from '../split/split';
import cartcontrol from '../cartcontrol/cartcontrol';
import ratingselect from '../ratingselect/ratingselect';

// const POSITIVE = 0;
// cont NEGATIVE = 1;
const ALL = 2;
export default{
  data() {
    return {
      foodFlag: false,
      selectType: ALL,
      onlyContent: true,
      desc: {
        all: '全部',
        positive: '满意',
        negative: '不满意'
      }
    };
  },
  props: {
    food: {
      type: Object
    }
  },
  methods: {
    show() {
      this.foodFlag = true;
      this.selectType = ALL;
      this.onlyContent = true;
      // 保证dom已加载
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$els.food, {
            click: true
          });
        } else {
          this.scroll.refresh();
        }
      });
    },
    hide() {
      this.foodFlag = false;
    },
    addFirst(event) {
      if (!event._constructed) {
        return;
      }
      this.$dispatch('cart.add', event.target);
      Vue.set(this.food, 'count', 1);
    },
    needShow(type, text) {
      if (this.onlyContent && !text) {
        return !1;
      }
      if (this.selectType === ALL) {
        return !0;
      } else {
        return type === this.selectType;
      }
    }
  },
  components: {
    split,
    cartcontrol,
    ratingselect
  },
  events: {
    'ratingtype.select'(type) {
      this.selectType = type;
      this.$nextTick(() => {
        this.scroll.refresh();
      });
    },
    'content.toggle'(flag) {
      this.onlyContent = flag;
      this.$nextTick(() => {
        this.scroll.refresh();
      });
    }
  },
  filters: {
    formatDate(time) {
      let date = new Date(time);
      return formatDate(date, 'YYYY-MM-DD hh:mm');
    }
  }
};
</script>
<style lang="less" rel="stylessheet/less">
@import "../../common/less/mixin.less";
.food{
  position:fixed;
  left:0;
  top:0;
  bottom:48px;
  z-index:30;
  width:100%;
  background:#fff;
  &.move-transition{
    transform:translate3d(0,0,0);
    transition:all 0.2s linear;
  }
  &.move-enter,&.move-leave{
    transform:translate3d(100%,0,0);
  }
  .food-content{
    .image-header{
      position:relative;
      width:100%;
      height:0;
      padding-top:100%;
      img{
        position:absolute;
        top:0;
        left:0;
        width:100%;
        height:100%;
      }
      .back{
        position:absolute;
        top:10px;
        left:0px;
        .icon-arrow_lift{
          display:block;
          padding:10px;
          font-size:20px;
          color:#fff;
        }
      }
    }
    .content{
      position:relative;
      padding:18px;
      .title{
        line-height:14px;
        margin-bottom:8px;
        font-size:14px;
        font-weight:700;
        color:rgb(7,17,27);
      }
      .detail{
        margin-bottom: 18px;
        line-height:10px;
        height:10px;
        font-size:0;
        .sell-count,.rating{
          font-size:10px;
          color:rgb(147,153,159);
        }
        .sell-count{
          margin-right:12px;
        }
      }
      .price{
        font-weight:700;
        line-height:24px;
        .now{
          margin-right:8px;
          font-size:14px;
          color:rgb(240,20,20);
        }
        .old{
          text-decoration:line-through;
          font-size:10px;
          color:rgb(147,153,159);
        }
      }
      .cartcontrol-wrapper{
        position:absolute;
        bottom:12px;
        right:12px;
      }
      .buy{
        position:absolute;
        right:18px;
        bottom:18px;
        z-index:10;
        height:24px;
        line-height:24px;
        padding:0 12px;
        box-sizing:border-box;
        border-radius:12px;
        font-size:10px;
        color:#fff;
        background:rgb(0,160,220);
        &.fade-transition{
          transition:all 0.2s;
          opacity:1;
        }
        &.fade-enter,&.fade-leave{
          opacity:0;
        }
      }
    }
    .info{
      padding:18px;
      .title{
        line-height:14px;
        margin-bottom:6px;
        font-size:14px;
        color:rgb(7,17,27);
      }
      .text{
        line-height:24px;
        padding:0 8px;
        font-size:12px;
        color:rgb(77,85,93);
      }
    }
    .rating{
      padding-top:18px;
      .title{
        line-height:14px;
        margin-left:18px;
        font-size:14px;
        color:rgb(7,17,27);
      }
    }
    .rating-wrapper{
      padding:0 18px;
      .rating-item{
        position:relative;
        padding:16px 0;
        .border-1px(rgba(7,17,27,0.1));
        .user{
          position:absolute;
          right:0;
          top:16px;
          line-height:12px;
          font-size:0;
          .name{
            display:inline-block;
            margin-right:6px;
            vertical-align:top;
            font-size:10px;
            color:rgb(147,153,159);
          }
          .avatar{
            border-radius:50%;
          }
        }
        .time{
          margin-bottom:6px;
          line-height:12px;
          font-size:10px;
          color:rgb(147,153,159);
        }
        .text{
          line-height:16px;
          font-size:12px;
          color:rgb(7,17,27);
          .icon-thumb_up,.icon-thumb_down{
            margin-right:4px;
            line-height:16px;
            font-size:12px;
          }
          .icon-thumb_up{
            color:rgb(0,160,220);
          }
          .icon-thumb_down{
            color:rgb(147,153,159);
          }
        }
      }
      .no-rating{
        padding:16px 0;
        font-size:12px;
        color:rgb(147,153,159);
      }
    }
  }


}
</style>



