<template>
  <div class="shopcart">
    <div class="content" @click="toggleList">
      <div class="content-left">
        <div class="logo-wrapper">
          <div class="logo" :class="{'highlight':totalCount}">
            <span class="icon-shopping_cart"></span>
          </div>
          <div class="num" v-show="totalCount>0">
            {{totalCount}}
          </div>
        </div>
        <div class="price" :class="{'highlight':totalPrice > 0}">
          ￥{{totalPrice}}
        </div>
        <div class="desc">
          另需配送费￥{{deliveryPrice}}元
        </div>
      </div>
      <div class="content-right" @click.stop.prevent="pay">
        <div class="pay" :class="payClass">
          {{payDesc}}
        </div>
      </div>
    </div>
    <div class="ball-container">
      <div class="ball"
        v-for="ball in balls"
        v-show="ball.show"
        transition="drop">
        <div class="inner inner-hook"></div>
      </div>
    </div>
    <div class="shopcart-list"
      v-show="listShow"
      transition="fold">
      <div class="list-header">
        <h1 class="title">购物车</h1>
        <span class="empty" @click="empty">清空</span>
      </div>
      <div class="list-content" v-el:list-content>
        <ul>
          <li class="food border-1px" v-for="food in selectFoods">
            <span class="name">{{food.name}}</span>
            <div class="price">
              <span>￥{{food.price * food.count}}</span>
            </div>
            <div class="cartcontrol-wrapper">
              <cartcontrol :food="food"></cartcontrol>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
  <div class="list-mask"
    v-show="listShow"
    transition="fade"
    @click="hideList">

  </div>
</template>

<script>
import BScroll from 'better-scroll';
import cartcontrol from '../cartcontrol/cartcontrol';
export default{
  data() {
    return {
      balls: [
        {
          show: false
        },
        {
          show: false
        },
        {
          show: false
        },
        {
          show: false
        }
      ],
      dropBalls: [],
      fold: true
    };
  },
  props: {
    selectFoods: {
      type: Array,
      default() {
        return [];
      }
    },
    deliveryPrice: {
      type: Number,
      default: 0
    },
    minPrice: {
      type: Number,
      default: 0
    }
  },
  computed: {
    totalPrice() {
      let total = 0;
      this.selectFoods.forEach((food) => {
        total += food.price * food.count;
      });
      return total;
    },
    totalCount() {
      let count = 0;
      this.selectFoods.forEach((food) => {
        count += food.count;
      });
      return count;
    },
    payDesc() {
      if (this.totalPrice === 0) {
        return `￥${this.minPrice}元起送`;
      } else if (this.totalPrice < this.minPrice) {
        return `还差${this.minPrice - this.totalPrice}元起送`;
      } else {
        return '去结算';
      }
    },
    payClass() {
      if (this.totalPrice >= this.minPrice) {
        return 'enough';
      } else {
        return 'not-enough';
      }
    },
    listShow() {
      if (!this.totalCount) {
        this.fold = true;
        return false;
      }
      let show = !this.fold;
      // 初始化betterscroll
      if (show) {
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BScroll(this.$els.listContent, {
              click: true
            });
          } else {
            this.scroll.refresh();
          }
        });
      }
      return show;
    }
  },
  methods: {
    drop(el) {
      for (let i = 0; i < this.balls.length; i++) {
        let ball = this.balls[i];
        if (!ball.show) {
          ball.show = !0;
          ball.el = el;
          this.dropBalls.push(ball);
          return;
        }
      }
    },
    toggleList() {
      if (!this.totalCount) {
        return;
      }
      this.fold = !this.fold;
    },
    empty() {
      this.selectFoods.forEach((food) => {
        food.count = 0;
      });
    },
    hideList() {
      this.fold = true;
    },
    pay() {
      if (this.totalPrice < this.minPrice) {
        return;
      }
      window.alert(`支付${this.totalPrice}元`);
    }
  },
  transitions: {
    drop: {
      beforeEnter(el) {
        let count = this.balls.length;
        while (count--) {
          let ball = this.balls[count];
          if (ball.show) {
            let rect = ball.el.getBoundingClientRect();
            let x = rect.left - 32;
            let y = -(window.innerHeight - rect.top - 22);
            el.style.display = '';
            el.style.webkitTransform = `translate3d(0,${y}px,0)`;
            el.style.transform = `translate3d(0,${y}px,0)`;
            let inner = el.getElementsByClassName('inner-hook')[0];
            inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
            inner.style.transform = `translate3d(${x}px,0,0)`;
          }
        }
      },
      enter(el) {
        /* eslint-disable no-unused-vars */
        let rf = el.offsetHeight;
        this.$nextTick(() => {
          el.style.webkitTransform = 'translate3d(0,0,0)';
          el.style.transform = 'translate3d(0,0,0)';
          let inner = el.getElementsByClassName('inner-hook')[0];
          inner.style.webkitTransform = 'translate3d(0,0,0)';
          inner.style.transform = 'translate3d(0,0,0)';
        });
      },
      afterEnter(el) {
        let ball = this.dropBalls.shift();
        if (ball) {
          ball.show = false;
          el.style.display = 'none';
        }
      }
    }
  },
  components: {
    cartcontrol
  }
};
</script>
<style lang="less" rel="stylessheet/less">
@import "../../common/less/mixin.less";
.shopcart{
  position:fixed;
  bottom:0;
  left:0;
  z-index:50;
  width:100%;
  height:48px;
  .content{
    display:flex;
    height:48px;
    background-color:#141d27;
    color:rgba(255,255,255,0.4);
    font-size:0;
    .content-left{
      flex:1;
      .logo-wrapper{
        display:inline-block;
        position:relative;
        top:-10px;
        margin:0 12px;
        padding:6px;
        height:56px;
        width:56px;
        box-sizing:border-box;
        vertical-align:top;
        border-radius:50%;
        background-color:#141d27;
        .logo{
          width:100%;
          height:100%;
          border-radius:50%;
          background:#2b343c;
          text-align:center;
          .icon-shopping_cart{
            font-size:24px;
            color:#80858a;
            line-height:44px;
          }
          &.highlight{
            background-color:rgb(0,160,220);
            .icon-shopping_cart{
              color:#fff;
            }
          }
        }
        .num{
          position:absolute;
          top:0;
          right:0;
          height:16px;
          width:24px;
          line-height:16px;
          text-align:center;
          border-radius:16px;
          font-size:9px;
          font-weight:700;
          color:#fff;
          background-color:rgb(230,20,20);
          box-shadow:0 4px 8px 0 rgba(0,0,0,0.4)
        }
      }
      .price{
        display:inline-block;
        vertical-align:top;
        margin-top:12px;
        padding-right:12px;
        line-height:24px;
        box-sizing:border-box;
        border-right:1px solid rgba(255,255,255,0.1);
        font-size:16px;
        font-weight:700;
        &.highlight{
          color:#fff;
        }
      }
      .desc{
        display:inline-block;
        vertical-align:top;
        margin-top:12px;
        margin-left:12px;
        line-height:24px;
        font-size:10px;
      }
    }
    .content-right{
      flex:0 0 105px;
      width:105px;
      .pay{
        height:48px;
        line-height:48px;
        text-align:center;
        font-size:12px;
        font-weight:700;
        background-color:#2b343c;
        &.not-enough{
          background-color:#2b343c;
        }
        &.enough{
          background-color:#00b43c;
          color:#fff;
        }
      }
    }
  }
  .ball-container{
    .ball{
      position:fixed;
      left:32px;
      bottom:22px;
      z-index:200;
      &.drop-transition{
        transition:all 0.4s cubic-bezier(0.49,-0.29,0.75,0.41);
        .inner{
          width:16px;
          height:16px;
          border-radius:50%;
          background:rgb(0,160,220);
          transition:all 0.4s linear;
        }
      }
    }
  }
  .shopcart-list{
    position:absolute;
    left:0;
    top:0;
    z-index:-1;
    width:100%;
    &.fold-transition{
      transition:all 0.5s;
      transform:translate3d(0,-100%,0);
    }
    &.fold-enter,&.fold-leave{
      transform:translate3d(0,0,0);
    }
    .list-header{
      height:40px;
      line-height:40px;
      padding:0 18px;
      background:#f3f5f7;
      border-bottom:1px solid rgba(7,17,27,0.1);
      .title{
        float:left;
        font-size:14px;
        color:rgb(7,17,27);
      }
      .empty{
        float:right;
        font-size:12px;
        color:rgb(0,160,220);
      }
    }
    .list-content{
      overflow:hidden;
      padding:0 18px;
      max-height:217px;
      background:#fff;
      .food{
        position:relative;
        padding:12px 0;
        box-sizing:border-box;
        .border-1px(rgba(7,17,27,0.1));
        .name{
          line-height:24px;
          font-size:14px;
          color:rgb(7,17,27);
        }
        .price{
          position:absolute;
          right:90px;
          bottom:12px;
          line-height:24px;
          font-size:14px;
          font-weight:700;
          color:rgb(240,20,20);
        }
        .cartcontrol-wrapper{
          position:absolute;
          right:0px;
          bottom:6px;
        }

      }
    }
  }
}
.list-mask{
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  z-index:40;
  backdrop-filter:blur(10px);
  &.fade-transition{
    transition:all 0.5;
    opacity:1;
    background:rgba(7,17,27,0.6);
  }
  &.fade-enter,&.fade-leave{
    opacity:0;
    background:rgba(7,17,27,0.6);
  }
}
</style>



