<template>
  <div class="header">
    <div class="content-wrapper">
      <div class="avatar">
        <img width="64" height="64" :src="seller.avatar" />
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">
          {{seller.description}}/{{seller.deliveryTime}}分钟送达
        </div>
        <div class="supports" v-if="seller.supports">
          <span class="icon" :class="classMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <div class="support-count" v-if="seller.supports" @click="showDetail">
        <span class="count">{{seller.supports.length}}个</span>
        <i class="icon-keyboard_arrow_right"></i>
      </div>
    </div>
    <div class="bulletin-wrapper" @click="showDetail">
      <span class="bulletin-title"></span>
      <span class="bulletin-text">{{seller.bulletin}}</span>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
    <div class="background">
      <img :src="seller.avatar" width="100%" height="100%"/>
    </div>
    <div class="detail" v-show="detailShow" transition="fade">
      <div class="detail-wrapper clearfix">
        <div class="detail-main">
          <h1 class="name">{{seller.name}}</h1>
          <div class="star-wrapper">
            <star :size="48" :score="seller.score"></star>
          </div>
          <div class="title">
            <div class="line"></div>
            <div class="text">优惠信息</div>
            <div class="line"></div>
          </div>
          <ul v-if="seller.supports" class="supports">
            <li class="support-item" v-for="item in seller.supports">
              <span class="icon" :class="classMap[seller.supports[$index].type]"></span>
              <span class="text">{{seller.supports[$index].description}}</span>
            </li>
          </ul>
          <div class="title">
            <div class="line"></div>
            <div class="text">商家公告</div>
            <div class="line"></div>
          </div>
          <div class="bulletin">
            <p class="content">{{seller.bulletin}}</p>
          </div>
        </div>
      </div>
      <div class="detail-close" @click="detailShow = !detailShow">
        <i class="icon-close"></i>
      </div>
    </div>
  </div>
</template>

<script>
import star from 'components/star/star';
  export default{
    data() {
      return {
        detailShow: false
      };
    },
    props: {
      seller: {
        type: Object
      }
    },
    created() {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    },
    methods: {
      showDetail() {
        this.detailShow = true;
      }
    },
    components: {
      star
    }
  };
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylessheet/less">
  @import "../../common/less/mixin.less";
  .header{
    position:relative;
    color:#fff;
    background:rgba(7,17,27,0.5);
    overflow:hidden;
    .content-wrapper{
      position:relative;
      padding:24px 12px 18px 24px;
      font-size:0;
      .avatar{
        display:inline-block;
        vertical-align:top;
        img{
          border-radius:2px;
        }
      }
      .content{
        display:inline-block;
        font-size:14px;
        margin-left:16px;
        .title{
          margin:2px 0 8px;
          .brand{
            display:inline-block;
            vertical-align:top;
            font-size:0;
            width:30px;
            height:18px;
            .bg-img('../../components/header/brand');
            background-size:30px 18px;
            background-repeat: no-repeat;
          }
          .name{
            margin-left:6px;
            font-size:16px;
            line-height:18px;
            font-weight:700;
          }
        }
        .description{
          margin-bottom:10px;
          line-height:12px;
          font-size:12px;
        }
        .supports{
          .icon{
            display:inline-block;
            vertical-align:middle;
            height:12px;
            width:12px;
            margin-right:4px;
            background-size:12px 12px;
            background-repeat:no-repeat;
            &.decrease{
              .bg-img('../../components/header/decrease_1');
            }
            &.discount{
              .bg-img('../../components/header/discount_1');
            }
            &.guarantee{
              .bg-img('../../components/header/guarantee_1');
            }
            &.invoice{
              .bg-img('../../components/header/invoice_1');
            }
            &.special{
              .bg-img('../../components/header/special_1');
            }
          }
          .text{
            line-height:10px;
            font-size:10px;
          }
        }
      }
      .support-count{
        position:absolute;
        bottom:18px;
        right:12px;
        padding:0 8px;
        height:24px;
        line-height:24px;
        border-radius:14px;
        background-color:rgba(0,0,0,0.2);
        text-align:center;
        .count{
          vertical-align:top;
          font-size:10px;
        }
        .icon-keyboard_arrow_right{
          margin-left:2px;
          line-height:24px;
          font-size:10px;
        }
      }
    }
    .bulletin-wrapper{
      position:relative;
      height:28px;
      line-height:28px;
      padding:0 22px 0 12px;
      overflow:hidden;
      white-space:nowrap;
      text-overflow:ellipsis;
      background:rgba(7,17,27,0.2);
      .bulletin-title{
        display:inline-block;
        vertical-align:top;
        margin-top:8px;
        width:22px;
        height:12px;
        .bg-img('../../components/header/bulletin');
        background-size:22px 12px;
        background-repeat:no-repeat;
      }
      .bulletin-text{
        vertical-align:top;
        margin:0 4px;
        font-size:10px;
        font-weight:200;
      }
      .icon-keyboard_arrow_right{
        position:absolute;
        right:12px;
        top:8px;
        font-size:10px;
      }
    }
    .background{
      position:absolute;
      top:0;
      left:0;
      width:100%;
      height:100%;
      z-index:-1;
      filter:blur(10px);
    }
    .detail{
      position:fixed;
      top:0;
      left:0;
      z-index:100;
      width:100%;
      height:100%;
      overflow:auto;
      background:rgba(7,17,27,0.8);
      transition:all 0.5s;
      backdrop-filter:blur(10px);
      &.fade-transition{
        opacity:1;
        background:rgba(7,17,27,0.8);
      }
      &.fade-enter,&.fade-leave{
        opacity:0;
        background:rgba(7,17,27,0);
      }
      .detail-wrapper{
        width:100%;
        min-height:100%;
        .detail-main{
          margin-top:64px;
          padding-bottom:64px;
          .name{
            line-height:16px;
            font-size:16px;
            text-align:center;
            font-weight:700;
          }
          .star-wrapper{
            margin-top:18px;
            padding:2px 0;
            text-align:center;
          }
          .title{
            display:flex;
            width:80%;
            margin:30px auto 24px;
            .text{
              padding: 0 12px;
              font-size:14px;
            }
            .line{
              flex:1;
              position:relative;
              top:-6px;
              border-bottom:1px solid rgba(255,255,255,0.2);
            }
          }
          .supports{
            width:80%;
            margin:0 auto;
            .support-item{
              padding:0 12px;
              margin-bottom:12px;
              font-size:0;
              &:last-child{
                margin-bottom:0;
              }
              .icon{
                display:inline-block;
                width:16px;
                height:16px;
                margin-right:6px;
                background-size:16px 16px;
                background-repeat:no-repeat;
                vertical-align:top;
                &.decrease{
                  .bg-img('../../components/header/decrease_2');
                }
                &.discount{
                  .bg-img('../../components/header/discount_2');
                }
                &.guarantee{
                  .bg-img('../../components/header/guarantee_2');
                }
                &.invoice{
                  .bg-img('../../components/header/invoice_2');
                }
                &.special{
                  .bg-img('../../components/header/special_2');
                }
              }
              .text{
                line-height:16px;
                font-size:12px;
              }
            }
          }
          .bulletin{
            width:80%;
            margin:0 auto;
            .content{
              padding:0 12px;
              line-height:24px;
              font-size:12px;
            }
          }
        }
      }
      .detail-close{
        position:relative;
        width:32px;
        height:32px;
        margin:-64px auto 0 auto;
        clear:both;
        font-size:32px;
      }
    }
  }
</style>



